.fallingArrow {
  position: absolute;
  left: 50%;
  top: -20px;
  height: 6px;
  width: 10px;
  color: #317c9c;
  filter: drop-shadow(0 0 6px #317c9c);
  animation: drop 10s linear infinite;
}

@keyframes drop {
  0% { 
    top: 0;
    opacity: 1;
    visibility: visible;
  }
  50% {
    opacity: .8;
    visibility: visible;
  }
  90% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    top: 90%;
    opacity: 0;
    visibility: hidden;
  }
}

.fallingArrow:nth-child(1) {
  left: 40%;
  animation-delay: 1s;
}

.fallingArrow:nth-child(2) {
  left: 30%;
  animation-delay: 2s;
}

.fallingArrow:nth-child(3) {
  left: 35%;
  animation-delay: 3s;
}

.fallingArrow:nth-child(4) {
  left: 60%;
  animation-delay: 4s;
}

.fallingArrow:nth-child(5) {
  left: 70%;
  animation-delay: 5s;
}

.fallingArrow:nth-child(6) {
  left: 54%;
  animation-delay: 5.5s;
}

.fallingArrow:nth-child(7) {
  left: 24%;
  animation-delay: 4.5s;
}

.fallingArrow:nth-child(8) {
  left: 63%;
  animation-delay: 6.5s;
}

.fallingArrow:nth-child(9) {
  left: 74%;
  animation-delay: 7.5s;
}

.fallingArrow:nth-child(10) {
  left: 44%;
  animation-delay: 8.5s;
}